.form {
  display: flex;
  flex-direction: column;
  width: 70%;
  margin: 30px 0;
  padding: 50px;
  background-color: #A8AEBF;
  padding-bottom: 30px;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin: 10px;
  background-color: #F0F0F2;
}
.row input {
  border-radius: 0;
  padding: 5px;
  border: 1px solid gray;
}
.row select {
  border-radius: 0;
  padding: 5px;
}
.row textarea {
  width: 50%;
  padding: 5px;;
}
.submit {
  width: 200px;
  height: fit-content;
  padding: 10px;
  border: none;
  color: white;
  font-size: larger;
  background-color: #1f4474;

}
.buttonContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 101%;
  margin-top: 20px;
}
